.skills-section {
    /* display: flex; */
    /* flex-direction: row; */
}

.skill-card {
    width: max-content;
    padding: 8px 16px;
    display: flex;
    height: 120px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    align-items: center;
    margin: 6px;
    /* border-radius: 8px; */
    /* background-color: aquamarine; */
}

.skill-icon {
    font-size: 50px;
}

.section-title {
    font-weight: bold;
}

.skill-name {
    font-size: 18px;
}

.skills {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.skills-section {
    display: flex;
    justify-content: space-between;
    margin: 12px 0px;
}

.skills-section-title {
    font-size: 24px;
    font-weight: 600;
}

.skills-list {
    display: flex;
    flex-wrap: wrap;
    max-width: 55%;
    justify-content: flex-end;
}

@media only screen and (max-width: 720px) {
    .skill-card {
        width: max-content;
        height: 100px;
    }
    .skill-icon {
        font-size: 60px;
    }
    .skills-list {
        max-width: 100%;
        justify-content: center;
    }
    .skills-section {
        flex-direction: column;
        align-items: center;
    }
}